.face {
  height: 100%;
}

.faceBox {
  position: relative;
  height: 105px;

  &:hover {
    cursor: pointer;
    &::after {
      content: "Wybierz";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: rgba(0, 0, 0, 0.5);
      width: 105px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
    }
  }
}

.faceSelected {
    position: relative;
    height: 105px;

  &::after {
    content: "Wybrane";
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -100%);
    background-color: rgba(0, 255, 13, 0.5);
    width: 105px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
}
